import { Component } from '@angular/core';
import { NavController,IonicPage,NavParams } from 'ionic-angular';
import { SettingProvider } from '../../providers/setting/setting';
import { UserService, UserDetail, CommonService } from '../../shared/';
import { AuthPage } from '../auth/auth';
import { ISubscription } from 'rxjs/Subscription';
@IonicPage()
@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage extends AuthPage{
  malePic = "assets/imgs/profile/male.png";
  femailPic =  "assets/imgs/profile/female.png";

  header_title:any;
  userDetail: UserDetail;
  protected userDetailSubscription : ISubscription ;
  constructor(public navCtrl: NavController, public navParams: NavParams, protected userService : UserService, protected commonService : CommonService) {
    super(navCtrl,navParams,userService)
    this.header_title = SettingProvider.pageSettings.AboutPage.pageTitle
    this.userDetail = new UserDetail();
  }

  //TODO: should use flatmap..
  ionViewDidLoad(){
    let loader = this.commonService.presentLoading();
    this.userDetailSubscription =this.userService.currentUser.subscribe(
      user => {
        this.userService.getUserDetail(user).subscribe(
          userDetail =>{
            this.userDetail = userDetail;
            if(this.userDetail){
              if(this.userDetail.gender){
                this.userDetail.pic = this.malePic
              }else{
                this.userDetail.pic = this.femailPic
              }
            }
          },
          error =>{
            this.commonService.dismissLoading(loader);
          },
          () =>{
          }
        )
      }
    );
  }

  ionViewCanLeave(){
    super.ionViewCanLeave();
    this.userDetailSubscription.unsubscribe;
  }

}



