import { TranslateModule } from '@ngx-translate/core';
import { MomentModule } from 'angular2-moment';


@NgModule({
  declarations: [
    AboutPage,
  ],
  imports: [
    IonicPageModule.forChild(AboutPage),
    HeaderPageModule,
    TranslateModule.forChild(),
    MomentModule
  ],
 
})
export class AboutPageModule {}


